import { Pipe, PipeTransform } from '@angular/core';
import { Technology } from '../models/job.interface';

@Pipe({
  name: 'technologyIcon'
})
export class TechnologyIconPipe implements PipeTransform {
  transform(technologies: Technology[]): Technology[] {
    if (technologies) {
      return technologies.filter(technology => technology.type === 'icon');
    }
    return [];
  }
}
