import { Component, OnInit, ViewChild } from '@angular/core';
import { AngularFireFunctions } from '@angular/fire/functions';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { of } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Component({
  selector: 'app-contact-form',
  templateUrl: './contact-form.component.html',
  styleUrls: ['./contact-form.component.css']
})
export class ContactFormComponent implements OnInit {
  contactForm: FormGroup;
  @ViewChild('contactFormDirective', { static: true }) private contactFormDirective: NgForm;

  isSending = false;

  constructor(
    private fb: FormBuilder,
    private fns: AngularFireFunctions,
    private snackBar: MatSnackBar
  ) {
    this.initForm();
  }

  ngOnInit() {}

  initForm() {
    this.contactForm = this.fb.group({
      name: this.fb.control('', [Validators.required, Validators.minLength(3)]),
      email: this.fb.control('', [Validators.required, Validators.email]),
      message: this.fb.control('', [
        Validators.required,
        Validators.minLength(25)
      ])
    });
  }

  send() {
    const contactFormValue = this.contactForm.value;
    const messageHtmlString: string = contactFormValue.message.replace(
      /(?:\r\n|\r|\n)/g,
      '<br>'
    );
    const isOnline: boolean = navigator.onLine;
    this.isSending = true;
    if (isOnline) {
      this.sendEmailFromFireFunctions(contactFormValue, messageHtmlString);
    } else {
      this.snackBar.open(`Activez votre connection internet`);
      this.isSending = false;
    }
  }

  private sendEmailFromFireFunctions(
    contactFormValue: any,
    messageHtmlString: string
  ) {
    this.fns
      .httpsCallable('sendEmail')({
        ...contactFormValue,
        message: messageHtmlString
      })
      .pipe(
        catchError(error => {
          if (error) {
            return of({ data: { responseCode: 'ko', info: null, error } });
          }
        })
      )
      .subscribe(response => {
        if (response.responseCode) {
          if (response.responseCode === 'ok') {
            this.snackBar.open('Message envoyé', undefined, { duration: 3000 });
          } else {
            this.snackBar.open('Message non envoyé', undefined, {
              duration: 3000
            });
          }
          this.resetForm();
        }
      });
  }

  resetForm() {
    this.contactForm.reset();
    this.contactFormDirective.resetForm();
    this.isSending = false;
  }
}
